import gallery from "./gallery-items.js";

// –°–æ–∑–¥–∞–Ω–∏–µ –∏ —Ä–µ–Ω–¥–µ—Ä —Ä–∞–∑–º–µ—Ç–∫–∏ –ø–æ –º–∞—Å—Å–∏–≤—É –¥–∞–Ω–Ω—ã—Ö –∏ –ø—Ä–µ–¥–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω–æ–º—É —à–∞–±–ª–æ–Ω—É.

function createMarkup(items) {
  return items
    .map(({ preview, original, description }) => {
      return `<li class="gallery__item">
  <a class="gallery__link" href="${original}">
    <img
      class="gallery__image"
      src="${preview}"
      data-source="${original}"
      alt="${description}"
    />
  </a>
</li>`;
    })
    .join(" ");
}

const refs = {
  gallery: document.querySelector(".js-gallery"),
  lightbox: document.querySelector(".js-lightbox"),
  lightboxImage: document.querySelector(".lightbox__image"),
  closeButton: document.querySelector(".lightbox__button"),
};

// –†–µ–∞–ª–∏–∑–∞—Ü–∏—è –¥–µ–ª–µ–≥–∏—Ä–æ–≤–∞–Ω–∏—è –Ω–∞ –≥–∞–ª–µ—Ä–µ–µ ul.js-gallery –∏ –ø–æ–ª—É—á–µ–Ω–∏–µ url –±–æ–ª—å—à–æ–≥–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è.

const markup = createMarkup(gallery);
refs.gallery.insertAdjacentHTML("beforeend", markup);

function onImageClick(e) {
  if (e.target.nodeName !== "IMG") {
    return;
  }
  e.preventDefault();
  refs.lightbox.classList.add("is-open");
  console.dir(e.target);
  changeLighboxImgSetting(e.target.src, e.target.dataset.source);
}

refs.gallery.addEventListener("click", onImageClick);

// –ü–æ–¥–º–µ–Ω–∞ –∑–Ω–∞—á–µ–Ω–∏—è –∞—Ç—Ä–∏–±—É—Ç–∞ src —ç–ª–µ–º–µ–Ω—Ç–∞ img.lightbox__image.

function changeLighboxImgSetting(alt, src) {
  refs.lightboxImage.alt = alt;
  refs.lightboxImage.src = src;
}

// –ó–∞–∫—Ä—ã—Ç–∏–µ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É button[data-action="close-lightbox"].

function onCloseClick(e) {
  refs.lightbox.classList.remove("is-open");
  // –û—á–∏—Å—Ç–∫–∞ –∑–Ω–∞—á–µ–Ω–∏—è –∞—Ç—Ä–∏–±—É—Ç–∞ src —ç–ª–µ–º–µ–Ω—Ç–∞ img.lightbox__image.
  // –≠—Ç–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –ø—Ä–∏ —Å–ª–µ–¥—É—é—â–µ–º –æ—Ç–∫—Ä—ã—Ç–∏–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞, –ø–æ–∫–∞ –≥—Ä—É–∑–∏—Ç—Å—è –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ, –º—ã –Ω–µ –≤–∏–¥–µ–ª–∏ –ø—Ä–µ–¥—ã–¥—É—â–µ–µ.
  changeLighboxImgSetting("", "");
}

refs.closeButton.addEventListener("click", onCloseClick);
